import React, { useState, useRef, useEffect } from "react";
import Matches from "../Matches";
import MatchesPending from "../MatchesPending";
// import Message from "../Message";
import MessagePending from "../MessagePending";
import "./index.css";

const MyWallFeature = () => {
  const [underlinePosition, setUnderlinePosition] = useState(null);
  const [currentWidth, setCurrentWidth] = useState(null);
  const firstOption = useRef(null);
  const [showMessages, setShowMessages] = useState(false);

  useEffect(() => {
    setUnderlinePosition(firstOption.current.offsetLeft);
    setCurrentWidth(firstOption.current.offsetWidth);
  }, [firstOption.current?.offsetLeft, firstOption.current?.offsetWidth]);

  const handleUnderline = (e) => {
    setUnderlinePosition(e.target.offsetLeft);
    setCurrentWidth(e.target.offsetWidth);
  };

  const handleShowOptions = (e) => {
    handleUnderline(e);
    if (e.target.childNodes[0].data === "Messages") setShowMessages(true);
    else setShowMessages(false);
  };

  // const user = {
  //   likes: [
  //     {
  //       avatar:
  //         "data:image/jpeg;base64,",
  //     },
  //     {
  //       avatar:
  //         "data:image/jpeg;base64,",
  //     },
  //     {
  //       avatar:
  //         "data:image/jpeg;base64,",
  //     },
  //   ],
  // };

  return (
    <section className="mywall__feature-section">
      <nav className="mywall__feature-parts">
        <div
          className="mywall__feature-options"
          onClick={handleShowOptions}
          ref={firstOption}
        >
          <p>Matches</p>
        </div>
        <div className="mywall__feature-options" onClick={handleShowOptions}>
          <p>Messages</p>
        </div>
        <div
          className="underline"
          style={{ left: `${underlinePosition}px`, width: `${currentWidth}px` }}
        ></div>
      </nav>
      <div className="mywall__feature-body">
        {!showMessages && (
          // <div className="likes-you">
          //   <Matches />
          // </div>
          <MatchesPending />
        )}
        {showMessages && (
          <div className="messages">
            <MessagePending />
          </div>
        )}
      </div>
    </section>
  );
};

export default MyWallFeature;
